import { parseParagraphs } from './utils/parseParagraphs';

const englishText = `
My name is Yu Xiulan. I owned three properties in Binhu District, Wuxi City, Jiangsu Province: my original home at No. 45 in Yong'an Village of Huazhuang Subdistrict, a newly constructed villa (temporarily numbered 45-1), and a productive pig farm measuring 343.7 square meters. I also held four mu (2,667 square meters) of contracted farmland, with half explicitly guaranteed under a "thirty-year unchanged" [1] contract. To this day, I still possess all the relevant contracts, property deeds, and keysnone of which I ever surrendered to the demolition authorities.

Each of my three properties occupied distinct locations: the new villa was built on Datong Road, the old house was in old Huazhuang Village, and the pig farm was situated in the silkworm fields on my farmland. The authorities forcibly seized these properties in three separate instances, yet when it came time to determine the resettlement compensation, they tried to lump them together as a single case. The new villa, spanning over 300 square meters and furnished with top quality materials, was built with the intention of preparing for my sons marriage. We had invested all our savings and even borrowed tens of thousands of yuan to construct it. My son suffered from a devastating bout of nephritis when he was 15 and only began to recover at 20. Due to his illness, he could not find a wife. Mom, Dad, he once said, we make our living raising, slaughtering, and selling pigs. The savings we deplete for the new house will build back, but a nice house will help me find a wife. The village commune, taking pity on us after my sons illness, granted us permission to establish a pig farm on my contracted land, allowing my son and me to raise pigs together. Over eight years, we built into a thriving operation with 178 pigs by the time of the forced demolition. Raising so many pigs was bitter work. My son managed the farm during daylight hours while I gathered feed scraps. We worked tirelessly, and my husband also worked himself to exhaustion at his factory job. Despite all our efforts and sacrifices, we never got to spend even a single night in our new villa before it was robbed from us.

The first blow came in October 2002, immediately after we completed the villa. Officials from both our village and subdistrict arrived with demolition notices, but without any legitimate documents or proper procedures. When my husband and I refused to comply, they brought in two local qingtou [2] (thugs) and an appraisal company. The so-called appraisal was less than perfunctorythey barely glanced around the property before making the assessment. By 2003, their tactics had escalated. The demolition team, working in concert with village officials and the thugs cosplaying local enforcers, began a systematic campaign to force residents into submission.

My husband wasnt born in the countryside. He had taken over his fathers factory job at 18 to feed his family after his father passed away. As a result, he didnt understand the complex rural land policies and ownership structures. On Sunday, March 29, 2003, he had a day off from work, the village officials summoned him shortly after lunch at 1 p.m. My son had tried to stop him from going, fearing something bad would happen, but my husband went anyway. 

At 9 p.m. that evening, after finishing my work at the pig farm, I grew increasingly uneasy and asked my son-in-law to drive me to the village office. I feared they might coerce my husband into signing something. When we arrived, the village party secretary [3], the head of the village, and the agricultural officer were guarding the entrance and wouldnt let me in. I had no choice but to return home, leaving my husband in their custody. When he finally returned at midnight, he was broken. I had no choice. I had to sign. Overcome with shame and despair, he sobbed continuously and repeatedly slapped himself in the face.

I asked him, How did this happen? He described how the village accountanta tall and physically imposing manhad bodily lifted him onto the signing table. The party secretary, village head, and the accountant surrounded him while someone from the demolition office repeatedly slammed the table, shouting, If you dont sign today, you wont even get a penny tomorrow! My husband eventually signed an agreement after enduring 11 hours of physical and psychological intimidation including pushing, shoving, and yelling. Notably, the agreement was blank [4], and my husband wasnt even the legal property owner.

Later, a demolition officer told my son, Your house is the best-built one in the area, but theyre offering you practically nothing. Don't you have any connectionssomeone who understands the inner workings of resettlementto help you get fair compensation? But we had no such connections, no insider knowledge. When my son finally learned the details of the compensation, the numbers were staggering in their inadequacy: our new villa, over 300 square meters of quality construction, was valued at a mere 166,000 ($22,952); the 343.7-square-meter pig farm that sustained our family was valued at just over 30,000 ($4,148); and our old house, approximately 300 square meters, built with earnings from eight years of my tofu-making business, was only compensated at 89,900 ($12,430). I worked tirelessly day-and-night to renovate this house, and it had been the site of my eldest daughters wedding after its completion and beam-raising. 

The revelation of these numbers broke something in my son. When he learned how they had devalued everything weve built, he lost all control, running wildly through the fields in a manic state. He eventually ran away from home, disappearing for several days, and even made two desperate trips to Nanjing that we only learned about later. 

Meanwhile, I was left alone to tend to the pigs and to manage our crumbling household. On April 2, 2003, while I was out running errands, they forcibly demolished our old house and pillaged our home. They pried up floorboards, scattered our pots, pans, and cooking utensils across the yard, and stole anything of value from the house. My husband was away petitioning [5] the authorities, my son was still missing, and my two daughters were out searching for their brother at train stations. Left alone without any help, I went to the demolition office to demand an explanation. I asked, Why did you destroy my house when I didnt sign anything? I still hold the deed and keys! They responded with cold indifference: Your husband signed an agreement. Thats enough.

When my husband returned home to the ruins of our house, he was so distraught that he attempted to take his own life. Thankfully, my daughters finally located my son and convinced him to return home to assuage his father. Though finally reunited, with nowhere else to go, our family of three was forced to move into our pig pen.

Life in the pig pen tested the limits of human endurance. My husband and son had to climb utility poles to siphon electricitya perilous practice that filled me with dread. We used well water initially but soon had to fetch river water with shoulder yolks. To drive us out, the authorities dug three enormous trencheseach as deep as a moataround our land. The pig feed we bought from neighboring villages could no longer be carried into our farm; we had to dump them in the nearby fields. One day, a kindhearted worker from a nearby construction site helped us fill one of the trenches, creating a makeshift path so I could sell pork at the market the following morning. That very night, a gang of thugs returned with bulldozers and destroyed the path. Under such dire conditions, our pigs began dying one by onesome from starvation and others from disease. Mosquitoes and flies made sleep nearly impossible, and the suffocating heat only intensified the sickening stench of death.

Not long after destroying our old house, the demolition office staff arrived at the pig farm hoping to fill out the blank agreement my husband was forced to sign. Standing at the entrance, the worker refused to step inside. "The moment I step inside, Im overwhelmed by the smell of pigs and death. I cant stand it." The weather was getting hotter, our basic amenities were completely stripped away in the siege, the dead pigs were decomposing where they fell, and flies were swarming in the air. Left with no choice, we fled the pig farm to my youngest daughters home. As soon as we left, the demolition crew immediately issued forced demolition notices for both the pig farm and villa. They brought in excavators and bulldozed everythingburying our surviving pigs alive alongside the dead beneath the rubble. It was the final blow to our livelihood.

Between April and May, temperatures soared to 38 to 40 C (100-104 F). My son and I made six desperate trips to the Wuxi Agricultural Companys appraisal office to challenge our property valuations. I asked, Why was our newly built villa appraised at the same rate as an old house and compensated so poorly? The appraisal manager admitted, The appraisal originally classified it as a villa, but the village thugs later tampered with it. He offered to help correct the appraisal and arranged a meeting with the village head for reassessment. However, the village head immediately dispatched thugs to block the appraisal team, threatening them and preventing any changes from being made. Before being forced to leave, the appraisal manager advised me, Your old house has both a land certificate and title deed, but they excluded the area from calculations. Your new villas appraisal has been manipulated. You should take legal action. Also, given your sons health issues and recent unemployment due to the demolitions, they should provide him with work. Following his guidance, I obtained two certifications from the village committee granting two storefronts for my sona promise that remains unfulfilled to this day.

Desperate and running out of options, I began making regular appeals to Binhu District Party Secretary Dai Jieping, often waiting outside his home. His elderly mother, who lived with him, offered me compassion: You must not give up on life. You must stay strong. Keep appealing step by step, eventually someone will listen to reason. I also submitted my petitions to Wuxi Municipal Party Secretary Mao Xiaoping, the Peoples Congress Director, and Secretary Dai himself. Although each level issued directives, local officials simply ignored them. Even when I escalated my case to the provincial level and requested information disclosure [6], the resulting provincial directives met the same fate. This experience revealed to me how thoroughly corruption could override the law.

On July 28, 2003, while we were temporarily living at my daughters home, we were informed at dawn that our villa was being demolished. My husband and I rushed to the site to find our meticulously built house being torn apart. The village head had long coveted the villas four large gates. By the time we arrived, his men had already dismantled the gates, loaded them onto a truck, and taken them to replace the gates of his own house. There were at least a dozen people at the site, but I was too overwhelmed to count. My husband stood by the site, weeping helplessly. After watching in despair for a while, we could do nothing but leave.

In 2005, I decided to seek justice through legal channels, placing my trust in lawyer Wu Xiliang from the Helie Law Firm on Qingshi Road in Donggang. "You will win this case," he assured me, leading me to borrow 20,000 ($2,765) from my in-laws for his services. However, it turned out that Attorney Wu had colluded with the local court, producing a perverse judgment that named my husband as the defendant based on his coerced signature. The court ruling not only validated the forced demolition but erased all our claims to the old house, pig farm, and farmland.

The first court hearing was scheduled right when my husband was hospitalized after a stroke. After 26 days in the hospital, he had just been discharged and was still paralyzed on one side. Unable to attend the hearing, the court ruled against us, dismissing our case claiming we had "no valid reason for absence." This incident completely shattered my sons trust in the legal system. He declared, The courts will never side with us ordinary people. My husband, frustrated and angry, lashed out at me, asking, Why did you even dare to sue the government? He even fought with me physically and broke everything in our home. Unable to bear the stain, I sought refuge with my sister, whose home lay near Secretary Dai Jiepings residence. From there, I continued my almost daily vigil outside Dais home, still seeking intervention.

In 2008, a second stroke claimed my husband's life when blood vessels ruptured in his brain. After his death, I pressed on, approaching the Binhu District Court for resolution. The presiding judge offered me a meal and advised, Keep escalating your case to the district and municipal levels. Perhaps someone elsewhere will help compensate you. I also filed a complaint against Attorney Wu with Beijings judiciary, and though the Beijing Judicial Bureau ordered him to pay compensation, his law firm's directorprotected by his position in the Binhu District People's Congressensured the order was never enforced.

On June 25, 2010, seeking official acknowledgment of our losses, I submitted information disclosure requests about both my demolished houses to the Wuxi Municipal Planning Bureau and Construction Bureau. Their response was shocking: Your house is not within the planning scope. There is no record of your property. They suggested that I escalate my case to the provincial level. Armed with this revelation, my son and I began appealing to provincial courts and the Peoples Supreme Court. Between 2010 and 2012, I made 18 trips to Beijing in search of justice. In March 2011, I finally obtained a transfer form from the State Petition Bureau to escalate our case. However, petition interceptors sent by Huazhuang Subdistrict caught me and escorted me directly to the Meiyuan Detention Center for 37 days of unlawful detention.

The detention center's cruelty was systematic. They fed me one bowl of porridge daily, which I hurled back at them in protest. After ten days, my daughter managed to find us and visited with fruits and supplements. The staff confiscated everything she brought and immediately transferred me and my son to a new jail. We were moved in the evening, blindfolded and placed in separate cars. We were sent to Xinan Nanyuan Guesthouse, where the inhumane treatment continued. They removed all protein from our meals and halved my sons portions before his eyes, forcing us to subsist on meager rations. When we protestedI smashing a chair against the window, my son overturning a tableeven the security guards began to sympathize. One guard eventually said, Swap our food with theirs. We can buy our own. These people will starve to death in here. After being starved, my teeth ached, and my stomach pain became unbearable, but I was denied medical care. One sympathetic guard took pity on me and spent 60 ($8) of his own money to purchase medication from a pharmacy. He said, none of you dares to help her, Ill do it, whats important is saving a life. I will never forget his act of kindness and humanity.

Three guards rotated in 8-hour shifts to watch me. There was no television, no phone, nothing but empty hours in a bare room. This confinement took a toll on my body and mind. By release, I had suffered two strokes requiring 42 days of hospitalization. I was reduced to skin and bones, barely resembling a living person.

During my detention, they targeted my son, telling him, Your father is already dead. Your mother might die soon too. Lets arrange a job for your wife. Under this manipulation, my son signed their agreement. They promised to arrange a government job for my daughter-in-law, but it was yet another empty promise. Instead, my daughter-in-law received a low-paying position at a property management company, earning only 1,200 ($166) monthly which was later increased to 1,500 ($207). When the property management company became privatized, my son grew concerned about his wifes job security and petitioned the district secretary. Their solution was a transfer to headquarters with her annual salary permanently capped at 36,000 ($5,000).  They gave my son an ultimatum: either renew the contract or they would arrange another job for her. Waiting for this arrangement left her unemployed from July 2021 until November 2023.

They had also promised to allocate my son a rent-free storefront for three years. However, after three years passed, they filed a fraudulent lawsuit against my son demanding over 200,000 ($27,653) in rent. The initial agreement they gave my son was merely an unsealed photocopy. My son, wary of their deception tactics, consulted a lawyer and then spent three days camping at their office until the document was properly stamped. In court, when confronted with the official seal, they quickly withdrew their claim.

Despite this small victory, we continue to face relentless legal battles over both my daughter-in-laws employment and my sons storefront. Even now, the courts refuse to issue final judgments, offering only interim rulings. As of November 2023, the third round of hearings continues in both Binhu and Liangxi District Courts, with no end in sight.

On August 17, 2004, as my son prepared for his wedding, they assigned him a 60-square-meter resettlement housing unit on the sixth floor. Every year, this unit leaked so heavily that it became almost uninhabitable. As for my own home, which originally spanned 300 square meters, I was given a mere 90-square-meter unit next door, also on the sixth floor. These are among the poorest quality units left in the resettlement housing stock. Nowadays they accuse me: Why did you accept the resettlement housing if you believed it unjust? The answer is simple we had no means of income; renting was financially impossible. With my husband's and my failing health, and my son's impending marriage, we were facing homelessness. 

After securing work for my daughter-in-law, we paused our petitions temporarily. However, my health took a turn for the worse. In 2015, I was diagnosed with pulmonary embolism. In 2018, I underwent surgery for a malignant tumor, leaving me partially paralyzed. Recovery was slow, but I eventually regained some mobility. When my son and I resumed our petitions in 2021, my pulmonary embolism relapsed, leading to another month of hospitalization.

My journey to the Supreme Court on August 31, 2023, offered a glimmer of hope when they confirmed that previous court errors could indeed be reexamined. However, the provincial petition office dismissed my case, saying, Forget everything else. Lets just settle the pig farm compensation. They transferred my case locally, where it was once again buried under fabricated documents. 

Since that Supreme Court visit, surveillance around me has intensified. Initially, 8 guards were posted outside my apartment4 during the day and 4 at night. They turned my hallway into their smoking lounge, throwing cigarette butts everywhere, leaving a foul stench. I pleaded with them, I have a serious lung condition. Please dont smoke here. They ignored me. In January 2024, my pulmonary embolism relapsed, and I had to spend over 8,000 ($1,100) on treatment. For 83 days, these thugs stationed outside prevented me from leaving my home. When I tried to check on our 60-square-meter apartment next door that we had rented out, they prevented me from going, restricting me from fulfilling even my landlord responsibilities. 

Although I called the police multiple times, no action was taken. Finally, in desperation, I grabbed the old butcher knife that I used for slaughtering pigs and declared, Ill fight you to the death if this continues! The district police arrived, accusing me of committing a violent crime. I responded, Im not the criminal. You are! Youve stolen everything I own and forced me into this miserable existence. They later reduced the number of guards to twoone during the day and one at nightbut the harassment persists. Even today, coming to this interview, one of them followed me to the bus station, relenting only after I promised to return by nightfall. If I escape to petition higher authorities, the guards would face fines for failing to contain me.

On April 16, 2024, I, along with two other petitioners, both elderly women, left our activists gathering spot at Little Park and boarded a bus to neighboring city Changzhous Long-Distance Bus Station. However, we were followed and intercepted in the public space. Four men came at us, dragging and manhandling me. They left my arms covered in bruises, and the surgical scar from my earlier lung operation was so severely aggravated that the pain kept me awake at night. Ive been applying medicated patches for almost three months, but the pain hasnt eased. They punched me in the face so hard that my eye bled, and they grips also left blisters on my body. 

Fortunately, over a dozen taxi drivers waiting near the transit station for passengers intervened to help. One driver used his car to block the two vehicles the thugs were driving while others called the police. When officers arrived, the attackers had the audacity to complain about the taxi drivers obstructing their cars. Once the path cleared, they shoved us into their cars and sped off. If not for the courage of those drivers, I might not have survived the ordeal.

Among our captors was a young man who was freshly discharged from the military. During the drive back to Wuxi, his hands were trembling as he said, I just finished my service and joined Huazhuang Subdistrict. I didnt know Id be doing something like this. Why are we making ordinary citizens suffer? I dont want this job anymoreIm resigning as soon as I get back. True to his word, Ive never seen him at the Huazhuang police station again.

The assault also left me with permanent joint damage in my left leg. Doctors suggested genetic testing and possibly administering molecularly targeted therapy, but the test alone would cost 20,000 ($2,765), far beyond my means. I told the doctors, Its too expensive. If I must die, Ill make one last standIll crawl to Beijing and die at Tiananmen Square.

A few days after the incident on April 20, 2024, while we were attempting to file a police report, over ten men attacked us again and dragged us into a car. Near the public security bureau, riot police surrounded us. One of the women had her head repeatedly slammed against the car door until it swelled severely, and her arms and hands were covered in bruises. They seized her phone and erased all the photos she had taken of our attackers.

We were then taken to the Huazhuang Police Station and separated into holding cells for criminals. They detained us until late at night, accusing us of disrupting public order and forcing us to admit to fabricated charges of obstructing official duties. The following day, they coerced us into providing fingerprints. Four men surrounded me, forcing my hand onto an ink pad for fingerprints, wiping it clean between impressions. Outnumbered four to one, I had no choice but to submit.

On April 23, 2024, during our official petition reception day, they tried to block me from attending. I prepared to leave my apartment at 1:30 p.m., but the guards at my door received orders to delay me until 2 p.m. When I finally arrived at the office of the Political and Legal Affairs Secretary, I was directed downstairs to the Petition Office. Within minutes, six riot police officers appeared, lifted me by my arms and legs, and again carried me to the Huazhuang police station. I protested, I can walk on my own, but was ignored. At the police station, over ten officers surrounded me, trying to force me to record a confession. I resisted until my son arrived and demanded to see their summons documents, accusing them of illegal detention and coercing confessions. They had no choice but to release me. I fled to my sisters home for refuge, but by 1 a.m., six riot police tracked us down. Recognizing the danger, my brother-in-law and sister called my son-in-law, who rushed over to take me to my daughters house. The next morning, when my daughter opened the door, she was horrified to find four people sleeping at the doorstep, still monitoring me.

They deny me even lifes most basic dignities, refusing to reinstate my rural medical insurance. As a result, every hospital visit is an out-of-pocket expense, adding to my already dire financial burden.

I have no farmland, no pigs, and no proper home. My husband worked tirelessly for 43 years but suffered two strokes due to the violence of forced demolitions and died after only receiving a few months of retirement benefits. My sons health is poor, and our financial situation is beyond precarious. Still, I refuse to give up. Every day, I write in my diary, building my only remaining weaponthe truth. I write with the hope that one day, the truth will come to light, and justice will prevail. 
`;

